/**
 * Flights model events
 */

'use strict';

import {EventEmitter} from 'events';
import Flights from './flights.model';
var FlightsEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
FlightsEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  Flights.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    FlightsEvents.emit(event + ':' + doc._id, doc);
    FlightsEvents.emit(event, doc);
  };
}

export default FlightsEvents;
